@value fontWeightStrong from '@/styles/typography.css';

.row {
    display: flex;
    flex-flow: row wrap;
    align-items:center;
    padding: 10px;
}

.row .name {
    flex-basis: 100%;
    font-weight: fontWeightStrong;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.row .values {
    flex-grow: 1;
    text-align: left;
    margin-right: 5px;
}

.values > div {
    line-height: 1.1em;
    padding: 5px;
}

.buttons {
    align-items: flex-end;
}

.buttons a:first-child {
    margin-right: 5px;
}

.width480 .name {
    flex-basis: 23%;
}

.width375 .values,
.width480 .values {
    display: flex;
}

.width480 .values > div {
    flex-basis: 50%;
}



