@value (orange50,
green50,
white50,
black50,
silver50) from '../colors.css';

@value ( fontSize,
btnFontSize) from '../typography.css';

@value ( btnPadding ) from '../spacing.css';

.widgetContainer {
    background-color: white50;
    color: black50;
}

.odd {
    background-color: silver50;
}

.btn {
    composes: btn from './base.css';
    background-color: white50;
    color: black50;
    border-width: 2px;
    border-radius: 4px;
}

.btnPrimary {
    composes: btnPrimary from './base.css';
    composes: btn;
}

.btnSuccess {
    composes: btnSuccess from './base.css';
    composes: btn;
}

.btnDanger {
    composes: btnDanger from './base.css';
    composes: btn;
    border-color: orange50;
}

.btnUp {
    composes: btnSuccess;
}

.btnDown {
    composes: btnDanger;
}

.colorPositive {
    color: green50;
}

.colorNegative {
    color: orange50
}



